import React, { Component } from 'react'
import { Grid, Row, Col, Modal } from "react-bootstrap";
import config from '../../constant/config.js';
const axios = require('axios');
export default class calculating extends Component {

    constructor(props) {
        super(props);
        this.state = {
            accountlist: [],
            accountDetailTable: [],
            customDepositeTable: [],
            customSwapTable: [],
            OpenOrderTable: [],
            HistoryOrderTable: [],

        }
    }

    fectheAllAccount() {
        let self = this;
        axios.get(config.baseurl + '/users/allAccounts')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({ accountlist: response.data.rows })
                }
            })
    }



    fetchAllOpenTrade() {
        let self = this;
        axios.get(config.baseurl + '/accounts/fetchAllOpenTrade')
            .then(function (response) {
                if (response.status == 200) {
                    self.setState({
                        accountDetailTable: response.data.accountTableDetails,
                        OpenOrderTable: response.data.openOrderData,
                        customSwapTable: response.data.customSwapTable,
                        customDepositeTable: response.data.customDepositeTable,
                        HistoryOrderTable:response.data.historyOrderData,
                    })
                }
            })
    }

    componentDidMount() {
        this.fectheAllAccount();
        this.fetchAllOpenTrade();
    }

    createTr = () => {
        return this.state.accountlist.map((item, index) => {
            return (
                <tr >
                    <td>{item.login}</td>
                    <td>{item.broker}</td>
                    <td>{item.alias}</td>
                </tr>
            )
        })
    }

    customSwap = () => {
        return this.state.customSwapTable.map((item, index) => {
            return (
                <tr >
                    <td>{item.account_id}</td>
                    <td>{item.open_value}</td>
                    <td>{item.close_value}</td>
                </tr>
            )
        })
    }

    customDeposite = () => {
        return this.state.customDepositeTable.map((item, index) => {
            return (
                <tr >
                    <td>{item.account_id}</td>
                    <td>{item.value}</td>
                </tr>
            )
        })
    }



    Open = () => {
        return this.state.OpenOrderTable.map((item, index) => {
            return (
                <tr >
                    <td>{item.account_id}</td>
                    <td>{item.ticket}</td>
                    <td>{item.open_time}</td>
                    <td>{item.order_type}</td>
                    <td>{item.lots}</td>
                    <td>{item.symbol}</td>
                    <td>{item.magic_number}</td>
                    <td>{item.open_price}</td>
                    <td>{item.last_price}</td>
                    <td>{item.sl}</td>
                    <td>{item.tp}</td>
                    <td>{item.commission}</td>
                    <td>{item.taxes}</td>
                    <td>{item.swap}</td>
                    <td>{item.profit}</td>
                </tr>
            )
        })
    }

    HistoryOrderData = () => {
        return this.state.HistoryOrderTable.map((item, index) => {
            return (
                <tr >
                    <td>{item.account_id}</td>
                    <td>{item.ticket}</td>
                    <td>{item.open_time}</td>
                    <td>{item.order_type}</td>
                    <td>{item.lots}</td>
                    <td>{item.symbol}</td>
                    <td>{item.magic_number}</td>
                    <td>{item.open_price}</td>
                    <td>{item.last_price}</td>
                    <td>{item.sl}</td>
                    <td>{item.tp}</td>
                    <td>{item.commission}</td>
                    <td>{item.taxes}</td>
                    <td>{item.swap}</td>
                    <td>{item.profit}</td>
                </tr>
            )
        })
    }


    createDeatailTable = () => {
        return this.state.accountDetailTable.map((item, index) => {
            return (
                <tr>
                    <td>{item.account_id}</td>
                    <td>{item.balance}</td>
                    <td>{item.equity}</td>
                    <td>{item.margin}</td>
                    <td>{item.margin_level}</td>
                    <td>{item.free_margin}</td>
                    <td>{item.profit}</td>
                    <td>{item.currency}</td>
                    <td>{item.last_seen}</td>
                </tr>
            )
        })
    }


    render() {

        return (
            <Grid fluid>

                <div style={{ background: '#ffffff' }}>
                    <Row>
                        <Col md={12}>
                            <div style={{ padding: '10px' }}>
                                <h5 className="text-center">Accounts</h5>

                                <table className="table lowercase table-sm">
                                    <thead>
                                        <tr>
                                            <th scope="col">login</th>
                                            <th scope="col">server</th>
                                            <th scope="col">alias</th>
                                        </tr>
                                    </thead>
                                    <tbody>

                                        {this.createTr()}

                                    </tbody>
                                </table>
                            </div>
                        </Col>
                    </Row>
                </div>


                <Row>
                    <Col md={3}>
                        <div style={{ padding: '10px' }}>
                            <h5 className="text-center">Account Details</h5>

                            <table className="table lowercase table-striped">
                                <thead>
                                    <tr>
                                        <th scope="col">account_id</th>
                                        <th scope="col">balance</th>
                                        <th scope="col">equity</th>
                                        <th scope="col">margin</th>
                                        <th scope="col">margin_level</th>
                                        <th scope="col">free_margin</th>
                                        <th scope="col">profit</th>
                                        <th scope="col">currency</th>
                                        <th scope="col">last_seen</th>
                                    </tr>
                                </thead>

                                <tbody>
                                    {this.createDeatailTable()}
                                </tbody>
                            </table>
                        </div>
                    </Col>
                </Row>



                <Row>
                    <Col md={6}>
                        <div style={{ padding: '10px' }}>
                            <h5 className="text-center">Custom Swap</h5>
                            <table className="table lowercase table-striped">

                                <thead>
                                    <tr>
                                        <th scope="col">account_id</th>
                                        <th scope="col">opne_value</th>
                                        <th scope="col">close_value</th>
                                    </tr>
                                </thead>
                                <tbody>

                                    {this.customSwap()}

                                </tbody>
                            </table>
                        </div>
                    </Col>

                    <Col md={6}>
                        <div style={{ padding: '10px' }}>
                            <h5 className="text-center">Custom Deposite</h5>
                            <table className="table lowercase table-striped">
                                <thead>
                                    <tr>
                                        <th scope="col">account_id</th>
                                        <th scope="col">value</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.customDeposite()}
                                </tbody>
                            </table>
                        </div>
                    </Col>
                </Row>

                <Col md={12}>
                    <div style={{ padding: '10px' }}>
                    <h5 className="text-center">Open Orders</h5>
                        
                        <table className="table lowercase table-responsive">

                            <thead>
                                <tr>
                                    <th scope="col">account_id</th>
                                    <th scope="col">ticket</th>
                                    <th scope="col">open_time</th>
                                    <th scope="col">order_type</th>
                                    <th scope="col">lots</th>
                                    <th scope="col">symbol</th>
                                    <th scope="col">magic_number</th>
                                    <th scope="col">open_price</th>
                                    <th scope="col">last_price</th>
                                    <th scope="col">sl</th>
                                    <th scope="col">tp</th>
                                    <th scope="col">commission</th>
                                    <th scope="col">taxes</th>
                                    <th scope="col">swap</th>
                                    <th scope="col">profit</th>

                                </tr>
                            </thead>
                            <tbody>

                                {this.Open()}
                            </tbody>
                        </table>
                    </div>
                </Col>


                <Col md={12}>
                    <div style={{ padding: '10px' }}>
                    <h5 className="text-center">History Orders</h5>
                        
                        <table className="table lowercase table-responsive">

                            <thead>
                                <tr>
                                    <th scope="col">account_id</th>
                                    <th scope="col">ticket</th>
                                    <th scope="col">open_time</th>
                                    <th scope="col">order_type</th>
                                    <th scope="col">lots</th>
                                    <th scope="col">symbol</th>
                                    <th scope="col">magic_number</th>
                                    <th scope="col">open_price</th>
                                    <th scope="col">last_price</th>
                                    <th scope="col">sl</th>
                                    <th scope="col">tp</th>
                                    <th scope="col">commission</th>
                                    <th scope="col">taxes</th>
                                    <th scope="col">swap</th>
                                    <th scope="col">profit</th>

                                </tr>
                            </thead>
                            <tbody>

                                {this.HistoryOrderData()}
                            </tbody>
                        </table>
                    </div>
                </Col>
            </Grid>
        )

    }
}
